        <div class="cart_shop clearfix">
            <div class="shop_info clearfix">
                <img src="../assets/images/buy-logo.png" alt="" class="shop_icon">
                <span class="shop_name">{{item.shop_name}}</span>
            </div>
            <div class="cart_free clearfix">
                <span class="free_tip">优惠券></span>
            </div>
        </div>
        <div class="cart_item">
          <van-checkbox @change="singerGoodsSelected()"  v-model="item.checked" checked-color="#c00000"></van-checkbox>
            <div class="cart_detial_box">
                <img :src="item.thumb_url" alt="" class="cart_product_link">
                <div class="item_names">{{item.goods_name}}</div>
                <div class="cart_weight">
                    <i class="my_weigth">重量:0.45kg</i>
                    <span class="my_color">颜色:AT800/16</span>
                </div>
                <div class="cart_product_sell">
                    <span class="product_money">￥<strong class="real_money">{{ item.price * item.buy_count | moneyFormat}}</strong></span>
                    <van-stepper @change="updateGoodsCount()" v-model="item.buy_count" integer />
                </div>
                <div class="cart_del clearfix">
                    <div class="del_top">
                    </div>
                    <div class="del_bottom" @click.stop="clickTrash(item.goods_id)">
                    </div>
                </div>
            </div>
        </div>
    </div>
    <footer class="cart_footer">
      <van-submit-bar
      :price="totalPrice"
      button-text="提交订单"
      >
      <van-checkbox v-model="isSelectedAll" @click.stop="selectedAll(isSelectedAll)" checked-color="#c00000">全选</van-checkbox>
