</template>
<script>
import { mapState } from 'vuex'
import { Dialog } from 'vant'
export default {
  data () {
    return {
      cartDatas: [],
      product_url: '',
      checked: true,
      isSelectedAll: false,
      totalPrice: 0
    }
  },
  computed: {
    ...mapState(['cartgoods'])
  },
  mounted () {
    // console.log(this.cartgoods, 'cartgoods')
    this.$store.dispatch('reqCartsGoods')
  },
  methods: {
    // 1. 单个商品的增加和减少
    updateGoodsCount () {
      // 计算商品的总价格
      this.getAllGoodsPrice()
    },
    // 2. 单个商品的选中和取消选中
    singerGoodsSelected (item) {
      // 2.1 判断是否全选
      this.hasSelectedAll()
      // 2.2 计算商品的总价格
      this.getAllGoodsPrice()
    },
    // 3. 是否全选
    selectedAll (isSelected) {
      // 3.1 总体控制
      this.isSelectedAll = !isSelected
      this.$store.dispatch('selectedAll', { isSelected })
      // 3.2 计算商品的总价格
      this.getAllGoodsPrice()
      // console.log(this.cartgoods, 'selectedAll')
    },
    // 4. 判断是否全选
    hasSelectedAll () {
      let flag = true
      // 4.1 遍历购物车的数据
      this.cartgoods.forEach((goods, index) => {
        if (!goods.checked) { flag = false }
      })
      // console.log(this.cartgoods.length)
      // 4.2 赋值
      this.isSelectedAll = flag
    },
    // 5. 计算商品的总价格
    getAllGoodsPrice () {
      let totalPrice = 0
      // 5.1 遍历
      this.cartgoods.forEach((goods, index) => {
        if (goods.checked) {
          totalPrice += goods.price * goods.buy_count
        }
      })
      // 5.2 赋值
      this.totalPrice = totalPrice * 100
    },
    // 6. 点击垃圾篓
    clickTrash (goods) {
      Dialog.confirm({
        title: '标题',
        message: '确定删除该商品吗?'
      }).then((action) => {
        if (action === 'confirm') {
          this.currentDelGoods = goods
          this.$store.dispatch('delGoodsSinger', { goods })
          if (this.cartgoods.length === 0) {
            this.isSelectedAll = false
          }
          // 计算商品的总价格
          this.getAllGoodsPrice()
        }
      })
    }
  },
  filters: {
    moneyFormat (money) {
      return '¥' + Number(money).toFixed(2)
    }
  }
}
</script>
